// @import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap";

body {
    font-family: 'Lato';
    font-size: 16px;
}

h1,h2,h3 {
    font-weight: bold;
}

.fa-btn {
    margin-right: 6px;
}

.jscroll-inner {
  overflow: hidden;
}

@for $i from 1 through 5 {
	.spacer#{$i*10} { margin-top:10px * $i; float:left; width: 100% };
}

#ajax-msg {
    position: fixed;
    z-index: 1000;
    right:10px;
    top: 10px;

    > div > * {
        margin: 0;

        border: 6px outset #999;
        border-radius: 10px;
        background: #eee;
        padding: 20px;
    }
}

.btn-sm {
    line-height: 1.4;
}

.btn.pull-right {
    margin-left: 5px;
}

section, figure {
    margin-bottom: 20px!important;
}

.panel-heading > *, .item h1 {
    margin: 0;
    display: inline-block;
}
.item header > * {
    white-space: nowrap;
}
.item h4 {
    color: black;
}
.panel-heading section {
    margin-top:5px;
    margin-bottom: 0!important;
}

section.photo-wrap {
    margin-bottom: 0!important;
}

#albums .item section.photo-wrap {
    height: 300px;
    overflow: hidden;
}

@-moz-document url-prefix() { /* Firefox */
    #albums .item section.photo-wrap {
        height: 258px;
    }    
}

.panel-albums article {
     border: 1px solid black;
     margin-bottom: 10px;
}

form#photo-mass-move > * {
    float: left;
    min-height: 34px;
    margin-right: 5px;
}

.action-links {
    margin-right: 5px;
}

#albums article, #gallery article {
    background: #f5f5f5;
}

#albums a:hover {
    text-decoration: none!important;

    > article {
        background: #ccc;
    }
}

ul.grid {
    list-style: none;
    padding: 0;
}
ul.grid, ul.grid li {
    vertical-align: baseline;        
}

ul.grid li {
    display: block;
}

@media (max-width: 991px) {
    #album-edit > div > div > .spacer30 { display: none; }
}

#changed-warning {
    margin-right: 5px;
    line-height: 2;
    font-weight: bold;
}

section.tags {
    position: relative;
    overflow-y: auto;
    width: 80%;
    height: 34px;
    
    nav {
        width: 100%;
        
        .btn {
            margin-bottom: 6px;
        }
    }
}

#albums {
    .item {
        padding: 4%;
        overflow: hidden;
    }
}

#gallery {
    
    .item {
        width: 100%;
        padding: 5%;
        padding-bottom: 0;
        overflow: hidden;
        border: 1px solid black;
        margin: 0 auto 20px;
        

        figure.img-wrap {
            width: 90%;
            height: 200px;
            overflow: hidden;
            margin: auto;
            width: 99%;
            height: auto;
            
                
            img {
                display: block;
                margin: auto;
                text-align: center;
                border: 1px solid gray;
            }
        }
        
        .caption {
            width: 92%;
            margin: 0 0 16px 4%;
            height: 40px;
            position: absolute;
            bottom: 73px;
            left: 0px;
            background: rgba(255, 255, 255, 0.8);
            text-align: center;
            color: black;
        }
        
        .img-icon {

            > .text-icon {
                padding: 0px 4px;
                font-size: 16px;
                border: 1px solid #CECECE;
                display: block;
                /*float: left;*/
                margin-top: 5px;                
            }
        }
    }
}

.photo-wrap {
    text-align: center;    
    
    img {
        width: 100%;
        height: auto;
    }
}

.photo-delete {
    display: inline-block;
}

.btn-empty {
    border: none;
    background: none;
}

.container .panel-heading {
    margin: 0;
}

